import React from "react";

export default class CardBase extends React.Component {
  componentWillMount() {
    this.setState({
      hover: false,
    })
  }

  render() {
    const baseStyle = {
      borderRadius: 12,
      backgroundColor: "white",
      marginBottom: 16,
      "box-shadow": "0px 1.5px 1.5px -1px black"
    };

    if (!this.props.interactive) {
      return <div style={baseStyle}>{this.props.children}</div>;
    }

    const extraStyle = this.state.hover
      ? {
         "box-shadow": "0px 3px 12px -2px rgba(0, 0, 0, 0.65)" }
      : {};

    const combinedStyles = {...baseStyle, ...extraStyle}
    return (
      <div onMouseEnter={() => this.setState({hover: true})} onMouseLeave={() => this.setState({hover: false})}
           style={combinedStyles}
      > {this.props.children} </div>
    )
  }
}


